/* Search Page Styles */

/* Search Form */
.search-form {
  display: flex;
  gap: 12px;
  align-items: center;
}

/* Bot de cerca - Hermes Style */
.search-form .filter-btn {
  padding: 12px 28px;
  background: linear-gradient(135deg, var(--color-primary-dark) 0%, var(--color-primary) 100%);
  border: none;
  border-radius: var(--radius-pill);
  color: var(--text-primary);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-semibold);
  cursor: pointer;
  transition: all var(--transition-normal);
  box-shadow: var(--shadow-primary);
}

.search-form .filter-btn:hover,
.search-form .filter-btn.primary {
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primary-dark) 100%);
  transform: translateY(-2px);
  box-shadow: var(--shadow-primary-lg);
}

.search-form .filter-btn:active {
  transform: scale(0.98);
}

/* Search Sections */
.search-section {
  margin-bottom: 40px;
}

.search-section .section-title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 20px;
}

.search-section .section-title svg {
  color: var(--color-primary);
}

/* TMDB Section - Styling for streaming results */
.search-section.tmdb-section {
  padding-top: 30px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.search-section.tmdb-section .section-title {
  color: var(--text-secondary);
}

.search-section.tmdb-section .section-title svg:first-child {
  color: #64b5f6;
}

/* Responsive */
@media (max-width: 768px) {
  .search-form {
    flex-direction: column;
    width: 100%;
  }

  .search-form .search-box {
    width: 100%;
  }

  .search-form .filter-btn {
    width: 100%;
  }
}
